
.breadmarket {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
.breadmarket label {
    display: block;
    font-size: 1.5em;
}
.breadmarket input,
  .breadmarket button {
    font-size: 2rem;
    padding: 1rem;
}
.breadmarket .small-font {
    font-size: .8em;
    color: gray;
}
.breadmarket .credits {
    color: #5c5;
    font-size: 1.2em;
    font-weight: bold;
}
.breadmarket .scroll-wrapper {
    max-height: 55vh;
    overflow: auto;
    padding-top: 1rem;
    padding-bottom: 1rem;
    -webkit-box-shadow: inset 0px 10px 20px -10px rgba(0, 0, 0, 0.05), inset 0px -10px 20px -10px rgba(0, 0, 0, 0.05);
            box-shadow: inset 0px 10px 20px -10px rgba(0, 0, 0, 0.05), inset 0px -10px 20px -10px rgba(0, 0, 0, 0.05);
}
.breadmarket .table th, .breadmarket .table td {
    padding-left: 1rem;
    padding-right: 1rem;
}
.breadmarket .table td:last-child {
    text-align: right;
}
.breadmarket .table tr:not(:first-child):hover {
    background-color: aliceblue;
    cursor: pointer;
}
.breadmarket .table.items tr:hover td {
    background-color: lightgoldenrodyellow;
}
.breadmarket .tables-container {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.breadmarket .tables-container > :not(:last-child) {
      margin-right: 4rem;
}
.breadmarket .timer {
    margin: 4rem 12%;
    border-top: 1px dashed #dadada;
    padding: 1rem 0;
    border-bottom: 1px dashed #dadada;
}
